import React from 'react';
import './CircleSelector.css';

function CircleSelector( props ) {
    return(
        <div className='CircleSelector'>
            <button
                key='1'
                onClick={() => props.handleNumSelection(1)}
                style={{
                    backgroundColor: props.selNum === 1 ? 'white' : '',
                    color: props.selNum === 1 ? 'lime' : 'white',
                  }}
            >
            {props.selNum === 1 ? 'Circle 1 Selected' : 'Select Circle 1' }
            </button>
            <button
                key='2'
                onClick={() => props.handleNumSelection(2)}
                style={{
                    backgroundColor: props.selNum === 2 ? 'white' : '',
                    color: props.selNum === 2 ? 'lime' : 'white',
                }}
            >
            {props.selNum === 2 ? 'Circle 2 Selected' : 'Select Circle 2' }
            </button>
            <button
                key='3'
                onClick={() => props.handleNumSelection(3)}
                style={{
                    backgroundColor: props.selNum === 3 ? 'white' : '',
                    color: props.selNum === 3 ? 'lime' : 'white',
                }}
            >
            {props.selNum === 3 ? 'Circle 3 Selected' : 'Select Circle 3' }
            </button>
            <button
                key='4'
                onClick={() => props.handleNumSelection(4)}
                style={{
                    backgroundColor: props.selNum === 4 ? 'white' : '',
                    color: props.selNum === 4 ? 'lime' : 'white',
                }}
            >
            {props.selNum === 4 ? 'Circle 4 Selected' : 'Select Circle 4' }
            </button>
        </div>

    )
}

export default CircleSelector;